* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar {
  /* background:rgba(0,0,0,0.6); */
  background-color: var(--first-color);
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  top: 0;
  
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  width: 80%;
  max-width: 1500px;
}

.navbar-logo {
  color: var(--white);
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}
.navbar-logo .logo-mov {
  margin: 0px 4px;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  align-items: center;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: var(--off-white);
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  list-style: none;
  height: 100%; /* the most important*/
}

.nav-links:hover {
  /* border-bottom: 4px solid var(--white); */
  color: var(--white);
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: var(--white);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    width: 100%;
    height: 70vh;
    position: absolute;
    top: 80px;
    left: +100%;
    opacity: 1;
    transition: all 0.5s ease;
    overflow-x: hidden;
    
  }

  .nav-menu.active {
    background-color: var(--first-color);
    /* background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%); */
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    margin: 10px 0;
    padding: 1rem 4rem;
    width: 100%;
    display: table;
    /* background-color: tomato; */
  }

  .nav-links:hover {
    background-color: transparent;
    color: var(--white);
    border-radius: 10px;
    box-shadow: 1px 1px 10px var(--white);
  }

  .navbar-logo {
    position: absolute;
    top: 10px;
    left: 10px;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 10px;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: var(--white);
    font-size: 2rem;
  }
  .form-control{
    display: flex;
    justify-content: center;
    text-align: center;
    /* background-color: violet; */
    width: 90%;
    margin-top: 20px;
}

  .form-control form{
    display: flex;
    width: 300px;
  }
}
